<template>
    <div class="container">
        <div class="mod-calculator">
            <div class="screen">
                <div class="tip">{{model}}</div>
                <div class="input">
                    <span>{{input}}</span>
                </div>
                <div class="output">
                    <span>{{output}}</span>
                </div>
                <div class="setting">
                    <span>男</span>
                    <input id="sex" type="checkbox" name="sex" value="1" @click="switchChange" @change="switchChange"/>
                    <label class="checkbox" for="sex">
                        <span class="switch"></span>
                    </label>
                    <span>女</span>
                    <a class="copyright" href="https://passer-by.com/" target="_blank">&copy; passer-by.com</a>
                </div>
            </div>
            <div class="panel">
                <div class="row">
                    <div class="col">
                        <div class="row"><button class="btn-first" data-value="爸爸" @click="bindInput">父</button></div>
                        <div class="row"><button class="btn-first" data-value="哥哥" @click="bindInput">兄</button></div>
                        <div class="row"><button class="btn-first" data-value="弟弟" @click="bindInput">弟</button></div>
                        <div class="row"><button class="btn-first" data-value="儿子" @click="bindInput">子</button></div>
                    </div>
                    <div class="col">
                        <div class="row"><button data-value="妈妈" @click="bindInput">母</button></div>
                        <div class="row"><button data-value="姐姐" @click="bindInput">姐</button></div>
                        <div class="row"><button data-value="妹妹" @click="bindInput">妹</button></div>
                        <div class="row"><button data-value="女儿" @click="bindInput">女</button></div>
                    </div>
                    <div class="col">
                        <div class="row"><button data-value="back" @click="bindInput">←</button></div>
                        <div class="row"><button data-value="老公" @click="bindInput" :disabled="sex==1">夫</button></div>
                        <div class="row"><button :class="{'btn-active':reverse}" data-value="exchange" @click="bindInput">⇌</button></div>
                        <div class="row"><router-link to="/help/">?</router-link></div>
                    </div>
                    <div class="col">
                        <div class="row"><button data-value="reset" @click="bindInput">↻</button></div>
                        <div class="row"><button data-value="老婆" @click="bindInput" :disabled="sex==0">妻</button></div>
                        <div class="row row2"><button class="btn-equal" data-value="equal" @click="bindInput">=</button></div>
                    </div>  
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Index',
        components: {

        },
        data(){
            return {
                model: '我称呼Ta',
                search: '',       //当前查询关系(输入缓存)
                input: '',        //输入显示
                output: '',       //输出显示
                s_switch: false,  //用户性别开关
                reverse: false,   //是否反向查询
                sex:1             //查询对象性别
            }
        },
        computed: {
        },
        methods:{
            switchChange: function(e) {
                window.console.log('#e#',e);
                var _ = this;
                _.search = '';
                _.input = '';
                _.output = '';
                _.s_switch = e.target.checked;
                _.sex = e.target.checked?0:1;
            },
            bindInput:function(e){
                var _ = this;
                var value = e.target.dataset.value;
                var search = _.search;      //读取输入缓存
                // var output = _.output;      //读取输出的数据
                var user_sex = _.s_switch?0:1;   //用户性别
                var result,r_result;
                var compute = function(){
                    if(!search){
                        _.input = '';
                        _.output = '';
                    }else{
                        result = window.relationship({text:search,sex:user_sex});
                        r_result = window.relationship({text:search,reverse:true,sex:user_sex});
                        if(value=='equal'&&search.indexOf('的')>-1){ //如果进行等号运算且具有“的”字运算符
                            _.search = result.length?result[0]:'';
                        }
                        if(_.reverse){
                            if(r_result.length){
                                _.input = search;
                                _.output = r_result.join('/');
                            }else{
                                _.input = search;
                                _.output = '--';
                            }
                        }else{
                            if(result.length){
                                _.input = search;
                                _.output = result.join('/');
                            }else{
                                _.input = search;
                                _.output = '--';
                            }
                        }
                    }
                };
                //输出处理
                switch (value) {
                    case 'back':
                    var index = search.lastIndexOf('的');
                    index = Math.max(0,index);
                    if(search){
                        search = search.substr(0,index);
                        value = search.split('的').pop();
                        _.search = search;
                        _.output = search;
                    }else{
                        _.search = '';
                        _.input = '';
                        _.output = '';
                    }
                    break;
                    case 'reset':
                        _.search = '';
                        _.input = '';
                        _.output = '';
                    break;
                    case 'exchange':
                        _.reverse = !_.reverse;
                        compute();
                        break;
                    case 'equal':
                        compute();
                        break;
                    default:
                        var arr = search.split('的');
                        if(arr.length>10){
                            _.search = search;
                            _.output = '--';
                        }else{
                            _.search = (search?search+'的'+value:value);
                            _.output = (search?search+'的'+value:value);
                        }
                }
                //查询对象性别
                var sex = _.sex;
                if(!value||!_.search){
                    sex = -1;
                }else if('爸爸,老公,儿子,哥哥,弟弟'.indexOf(value)>-1){
                    sex = 1;
                }else if('妈妈,老婆,女儿,姐姐,妹妹'.indexOf(value)>-1){
                    sex = 0;
                }
                //模式切换
                var obj = sex<0?'Ta':(sex?'他':'她');
                var text = _.reverse?obj+'称呼我':'我称呼'+obj;
                _.sex = sex<0?user_sex:sex;
                _.model = text;
            },
        },
        created(){
            
        }
    }
</script>

<style lang="scss" scoped>
    .container{
        height: 100%;
        .inner{
            height: 100%;
        }
    }
    .mod-calculator{
        height: 100%;
        background: #333;
        .row{
            height: 100%;
            flex: 1;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: space-between;
            box-sizing: border-box;
        }
        .row2{
            flex: 2;
        }
        .col{
            width: 100%;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: space-between;
            box-sizing: border-box;
        }
        span{
          vertical-align: middle;
        }
        .screen{
            width: 100%;
            height: 40%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: space-between;
            .tip{
                width:100%;
                padding: 0 0.15rem;
                height: 0.3rem;
                line-height: 0.3rem;
                color: #bbb;
                box-sizing: border-box;
            }
            .input,.output{
                width:100%;
                padding: 0 0.15rem 0.15rem 0.1rem;
                text-align: right;
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                justify-content: center;
            }
            .input{
                flex: 2;
                font-size: 0.16rem;
                color: #bbb;
            }
            .output{
                flex: 3;
                font-size: 0.21rem;
                color: #fff;
            }
            .setting{
                width: 100%;
                padding: 0 0.15rem;
                height: 0.4rem;
                background: rgba(0,0,0,.15);
                line-height: 0.4rem;
                font-size: 0.16rem;
                color: rgba(255,255,255,.9);
                box-sizing: border-box;
                input[type="checkbox"]{
                    display: none;
                }
                .checkbox {
                    margin: 0 0.07rem;
                    vertical-align: middle;
                    user-select: none;
                    position: relative;
                    display: inline-block;
                    width: 0.40rem;
                    height: 0.20rem;
                    background: rgba(255,255,255,0.15);
                    line-height: 0.20rem;
                    vertical-align: middle;
                    text-align: center;
                    border-radius: 0.10rem;
                }
                .switch {
                    position: absolute;
                    left: 0;
                    width: 0.20rem;
                    height: 0.20rem;
                    content: '';
                    background: rgba(255,255,255,0.72);
                    border-radius: 0.10rem;
                }
                input:checked+.checkbox .switch {
                    left: auto;
                    right: 0;
                }
                .copyright{
                    float: right;
                    font-size: 0.14rem;
                    color:  #484848;
                }
            }
        }
        .panel{
            width: 100%;
            height: 60%;
            font-size: 0.16rem;
            button,a{
                position: relative;
                background: transparent;
                border:none;
                border-left:0.01rem solid #444;
                border-top:0.01rem solid #444;
                font-family: Arial,Helvetica,"Microsoft Yahei";
                font-size: 0.18rem;
                color: rgba(255,255,255,.9);
                flex: 1;
                display:flex;
                align-items:center;
                justify-content: center;
                text-align: center;
                vertical-align: middle;
                border-radius: 0.02rem;
                &:active{                
                    background-color: rgba(0,0,0,.1);
                    color: rgba(255,255,255,.8);
                }
            }
            button[disabled]{
                background: transparent;
                color: rgba(255,255,255,.3);
            }
            .btn-first{
                border-left:none;
            }
            .btn-equal{
                background: #e7763e;
            }
            .btn-equal-hover{
                background: #e66747;
            }
            .btn-active{
                background-color: rgba(0,0,0,.2);
                color: rgba(255,255,255,.7);
            }
        }
    }

</style>